<template>
	<div>
		{{name}}
	</div>
</template>

<script>
	export default{
		created(){
			this.getData()
		},
		data(){
			return {
				id:this.$route.params.id,
				list:{},
				name:""
			}
		},
		methods:{
//			http://m.maoyan.com/ajax/cinemaDetail?cinemaId=117
			getData(){
				this.$http.get("/my/ajax/cinemaDetail?cinemaId="+this.id).then(res=>{
					console.log(res)
					this.list=res.data.cinemaData;
					this.name=this.list.nm
				})
			}
		}
	}
</script>

<style>
</style>


